.container-beneficio-uno {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, auto);
  border-radius: 10px;
  background: linear-gradient(117deg, rgba(248, 248, 248, 0.9948354342) 42%, rgb(255, 116, 0) 100%);
}
@media (max-width: 991px) {
  .container-beneficio-uno {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, auto);
    border-radius: 10px;
    background: linear-gradient(180deg, rgba(248, 248, 248, 0.9948354342) 42%, rgb(255, 116, 0) 100%);
  }
}
