footer {
  width: 100%;
  border-top: 1px solid #ff4419;
  padding-bottom: 20px;
}
footer .footer {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
footer .footer ul {
  display: flex;
  flex-direction: row;
  padding-left: 0px;
}
footer .footer li:not(:last-child) {
  margin-right: 15px;
}
footer .footer li {
  list-style: none;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 0.05em;
}
footer .footer li img {
  margin-right: 5px;
  height: 20px;
}
@media (max-width: 991px) {
  footer .footer {
    justify-content: end;
    flex-direction: column;
  }
  footer .footer ul {
    display: flex;
    flex-direction: row;
    padding-left: 0px;
  }
  footer .footer li {
    list-style: none;
  }
}

